import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import name_anim from "../images/name_anim.gif"

const NamePlate = () => (
  <div style={{marginBottom: '50px', marginTop: '120px'}}>
    
    
	<div style={{textAlign: 'center'}}>
	<img className="name" src={name_anim}/>
	<br/>
	<p className="nameplate-role"><b>{'<'}software engineer{'/>'}</b></p><p className="nameplate-title"> and <i>graphic designer</i></p>
	</div>
  </div>
)

export default NamePlate
