import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import github_icon from "../images/github_icon.png"
import linkedin_icon from "../images/linkedin_icon.png"
import resume_icon from "../images/resume_icon.png"
import projects_icon from "../images/projects_icon.png"
import Footer from "./footer"

const ButtonLayout = () => (
  <div className="icons">
    
	<ul>
		<li>
			<a href="https://github.com/jimmyshi360" target="_blank">
				<img className="icon hovered git" src={github_icon}/>
			</a>
		</li>
		<li>
			<a href="https://github.com/jimmyshi360" target="_blank">
				<img className="icon hovered proj" src={projects_icon}/>
			</a>
		</li>
		<li>
			<a href="https://www.linkedin.com/in/jimmyshi360/" target="_blank">
				<img className="icon hovered linked" src={linkedin_icon}/>
			</a>
		</li>
		<li>
			<Link to="/page-2/">
				<img className="icon hovered resume" src={resume_icon}/>
			</Link>
		</li>
	</ul>
  </div>
)

export default ButtonLayout